html {
  font-size: 100%;
  box-sizing: border-box;
}

body,
ul {
  margin: 0;
  padding: 0;
  color: var(--white);
}

ul {
  list-style: none;
}

a {
  color: var(--white);
}

h1,
h2,
h3,
h4,
h5,
p {
  margin-top: 0;
  margin-bottom: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}